body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  background-color: white; }

.app__header {
  height: 50px;
  width: 100%;
  background-color: lightblue; }

.app__footer {
  height: 50px;
  width: 100%;
  background-color: lightblue; }

.main__wrapper {
  display: -webkit-flex;
  display: flex; }

.filters__container {
  width: 40vw;
  border: 1px solid black; }

.books__container {
  width: 60vw; }

.books__list {
  list-style: none;
  margin: 0;
  padding: 0; }

.books__list-item {
  border: 1px solid black; }

.features__container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end; }

.search__title-label {
  display: block; }

.filter__discount-label {
  display: block; }

